class PrintButton {
  constructor(button) {
    this.button = button;
    this.activeClass = 'print-agenda-active';
    this.body = document.querySelector('body');
  }

  // Print Button Trigger
  buttonTrigger() {
    this.button.addEventListener('click', () => {
      this.body.classList.add(this.activeClass);
      window.print();
    });
  }

  // Set After Print
  setAfterPrint() {
    // Not webkit
    if (! window.matchMedia) {
      window.onafterprint = () => {
        if (this.body.classList.contains(this.activeClass)) {
          this.doAfterPrint();
        }
      };
    } else {
      // webkit
      const mediaQueryList = window.matchMedia('print');
      mediaQueryList.addListener((mql) => {
        if (! mql.matches) {
          // on afterprint
          if (this.body.classList.contains(this.activeClass)) {
            this.doAfterPrint();
          }
        }
      });
    }
  }

  // Stuff to do after print
  doAfterPrint() {
    this.body.classList.remove(this.activeClass);
    // return to agenda block. corrects jump after print prompt display
    document.getElementById('agenda').scrollIntoView();
  }

  // Init
  init() {
    if (this.button) {
      this.buttonTrigger();
      this.setAfterPrint();
    }
  }
}

export default PrintButton;
