class Companies {
  constructor(sections) {
    this.sections = sections;
    Array.from(this.sections).forEach((section) => {
      const filter = section.querySelector('[data-company-filter]');
      const companies = section.querySelectorAll('.company-list__item');
      if (filter && companies) {
        const links = filter.querySelectorAll('a');
        const list = filter.querySelectorAll('li');
        Array.from(links).forEach((link) => {
          link.addEventListener('click', (event) => {
            event.preventDefault();
            Array.from(list).forEach((listItem) => {
              listItem.classList.remove('active');
            });
            link.parentElement.classList.add('active');
            const target = link.dataset.filter;
            if ('' === target) {
              this.clearFilter(companies);
            } else {
              this.updateFilter(companies, target);
            }
          });
        });
      }
    });
  }
  clearFilter(companies) {
    Array.from(companies).forEach((el) => {
      el.setAttribute('aria-hidden', false);
    });
  }
  updateFilter(companies, target) {
    Array.from(companies).forEach((el) => {
      if (el.classList.contains(`company-filter--${target}`)) {
        el.setAttribute('aria-hidden', false);
      } else {
        el.setAttribute('aria-hidden', true);
      }
    });
  }
}

export default Companies;
