// Put events page javascript here

import '../../scss/events/loader.scss';
import Speakers from './speakers';
import Companies from './companies';
import Agenda from './agenda';

if (module.hot) {
  module.hot.accept();
}

const formSkipAddingSelectLabel = [2703, 2715];

MktoForms2.whenRendered((form) => {
  const formEl = form.getFormElem();
  const formId = form.getId();
  let fieldId = 0;
  if (- 1 !== formSkipAddingSelectLabel.indexOf(formId)) {
    formEl.addClass('skip-adding-select-label');
  }
  formEl.find('.mktoField').each(function () {
    const el = jQuery(this);
    const parent = el.parents('.mktoFormRow');
    let type = false;

    if (el.is('input')) {
      if ('text' === el.attr('type')
        || 'email' === el.attr('type')
        || 'tel' === el.attr('type')) {
        type = 'input';
      }
      if ('checkbox' === el.attr('type')) {
        type = 'checkbox';
      }
      if ('radio' === el.attr('type')) {
        type = 'radio';
      }
    }

    if (el.is('select')) {
      type = 'select';
    }
    if (type) {
      parent.addClass(`mktoField--${type}`);
      parent.addClass(`mktoField--${type}-${fieldId}`);
    }
    if (el.is('[multiple]')) {
      el.attr('size', el.find('option').length);
      parent.addClass('mktoField--multiple');
    }
    if (formEl.find('.mktoPlaceholder').length) {
      formEl.addClass('mktoForm--conditionals');
    }
    formEl.find('.mktoPlaceholder').each(function () {
      jQuery(this).parents('.mktoFormRow').addClass('has-placeholder');
    });
    fieldId += 1;
  });
});

MktoForms2.whenReady((form) => {
  const formEl = form.getFormElem();

  formEl.find('.mktoFormRow').find('label').each(function () {
    const $label = jQuery(this);
    const labelText = $label.text().replace('*', '');
    const labelTextNew = labelText.replaceAll('>>>', ' <br />');
    $label.html(labelTextNew);
  });

  formEl.find('.mktoLabel').each(function () {
    const $self = jQuery(this);
    const inputList = $self.siblings().find('input');

    if (1 === inputList.length) {
      const $input = $self.siblings().find('input');
      const text = $self.html();

      if ('checkbox' === $input.attr('type')) {
        const $label = $input.siblings();

        if (0 >= $self.children('span').length) {
          $label.html(text);
          $self.hide();
          $label.addClass('custom-label');
          $input.addClass('custom-input');
        }
      }
    } else {
      $self.siblings('.mktoCheckboxList').addClass('checkbox-list');
      $self.addClass('checkboxs-title');
    }
  });
});

document.addEventListener('DOMContentLoaded', () => {
  const speakersFilter =
    document.querySelectorAll('.event-component--speakers-v2');
  if (speakersFilter) {
    // eslint-disable-next-line no-unused-vars
    const speakers = new Speakers(speakersFilter);
  }

  const companiesFilter =
    document.querySelectorAll('.event-component--company-list');
  if (companiesFilter) {
    // eslint-disable-next-line no-unused-vars
    const companies = new Companies(companiesFilter);
  }

  const agendaFilter =
    document.querySelectorAll('.event-component--agenda-v2');
  if (agendaFilter) {
    // eslint-disable-next-line no-unused-vars
    const agenda = new Agenda(agendaFilter);
  }
});
