import PrintButton from './printButton';

class Agenda {
  constructor(sections) {
    this.sections = sections;
    Array.from(this.sections).forEach((section) => {
      const filter = section.querySelector('[data-agenda-filter]');
      const speakers = section.querySelectorAll('.agenda-list__item');
      const printButton = section.querySelector('[data-print-agenda]');
      if (filter && speakers) {
        const links = filter.querySelectorAll('a');
        const list = filter.querySelectorAll('li');
        const print = new PrintButton(printButton);
        print.init();
        Array.from(links).forEach((link) => {
          link.addEventListener('click', (event) => {
            event.preventDefault();
            Array.from(list).forEach((listItem) => {
              listItem.classList.remove('active');
            });
            link.parentElement.classList.add('active');
            const target = link.dataset.filter;
            if ('' === target) {
              this.clearFilter(speakers);
            } else {
              this.updateFilter(speakers, target);
            }
          });
        });
      }
    });
  }
  clearFilter(speakers) {
    Array.from(speakers).forEach((el) => {
      el.setAttribute('aria-hidden', false);
    });
  }
  updateFilter(speakers, target) {
    Array.from(speakers).forEach((el) => {
      if (el.classList.contains(`agenda-team--${target}`)) {
        el.setAttribute('aria-hidden', false);
      } else {
        el.setAttribute('aria-hidden', true);
      }
    });
  }
}

export default Agenda;
